:root {
  --blue: #00c0ff;
  --pink: #ff458c;
  --black: var(--black);
}

html {
  font-size: 1rem;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

body {
  font-size: 62.5%;
  font-family: "Open Sans", sans-serif;
  font-size: 1.4rem;
  line-height: 1.3;
  font-weight: 100;
  color: var(--black);
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 4vw;
  font-family: "Montserrat", sans-serif;
  font-weight: 900;
  text-transform: uppercase;
  font-style: italic;
  color: var(--black);
  line-height: 1.3;
}

h1 span {
  background: var(--bgp-pink, #ff458c);
  color: white;
  padding: 0 30px 0 0;
}

.u-bigger-title {
  font-size: 10vw;
  display: inline;
}

h2 {
  font-family: "Montserrat", sans-serif;
  font-weight: 900;
}

nav {
  position: sticky;
  bottom: 0;
  top: 0;
  width: 100%;
  background: white;
  height: 80px;
  background: var(--blue);
  z-index: 1;
  text-transform: uppercase;
  ul {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
    height: 100%;
    margin: 0 10px;
    padding: 0;
  }
  li {
    list-style: none;
    text-align: center;
  }
  a {
    color: white;
    font-weight: 500;
    padding: 5px 10px;
  }
}

h2 {
  color: var(--blue);
  text-transform: uppercase;
}

section {
  width: 100%;
  position: relative;
}

.c-main-title {
  overflow: hidden;
  min-height: calc(100vh - 80px);
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: center;
}

.c-main-title__content {
  width: 30vw;
  min-width: 400px;
  margin-bottom: 80px;
}

.c-main-title__heading {
  width: 50vw;
}

.c-main-title__content,
.c-main-title__heading {
  padding: 20px;
}

p {
  font-size: 1.4rem;
  line-height: 1.3;
  font-weight: 100;
  color: var(--black);
}

.section-content {
  max-width: 860px;
  margin: 0 auto;
  padding:0 10px;
  ul li,
  ol li {
    margin-bottom: 20px;
  }
}

#manifeste {
  padding-top: 80px;
}

.c-main-title__link {
  color: white;
  padding: 5px 10px;
  font-weight: 600;
  text-decoration: none;
  background: var(--blue);
  svg {
    width: 15px;
    fill: white;
    margin-right: 10px;
  }

  &:hover {
    svg {
      transition: 0.5s;
      margin-right: 5px;
      margin-left: 5px;
    }
  }
}

.signataires {
  background:var(--pink);
  color:white;
}

.signataires__text {
  margin:0 auto;
  padding:50px 10px;
  max-width: 860px;
  text-align: center;
  a {color:white;}
}

@media (max-width: 960px) {
  .c-main-title {
    width: auto;
    flex-flow: column nowrap;
    align-items: flex-start;
    justify-content: center;
    h1 {
      font-size: 10vw;
      text-align: left;
    }
  }

  .c-main-title__heading {
    width: auto;
  }

  .c-main-title__content {
    min-width: auto;
    width: auto;
  }

  .u-bigger-title {
    font-size: 15vw;
  }
}
@media (max-width: 960px) {
  nav {
    ul {
      justify-content: center;
      align-items: center;
      a {
        font-size: 1rem;
      }
    }
  }
}
